@import url(https://fonts.googleapis.com/css?family=Poppins|Montserrat:100,200,400,500);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Poppins, Montserrat, sans-serif;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

div.App {
  background-color: #202221;
  width: 100vw;
  height: 100vh;
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
}

div#start {
  display: flex;
  width: 40vw;
  height: 300px;
  justify-content: space-between;
  border-radius: 10px;
}

div#js-box {
  background-color: #cfcc2c;
  width: 50%;
  border-radius: 5px 0px 0px 5px;
  font-size: 100px;
  color: #202221;
  text-align: center;
}

div#js-box span {
  background-color: #202221;
  width: 9vw;
  height: 9px;
  display: block;
}

div#js-box h1 {
  position: relative;
  top: -2vh;
}

div#pro-box {
  background-color: #2a2e2c;
  width: 50%;
  border-radius: 5px 0px 0px 5px;
}

div#pro-box span {
  height: 5px;
  width: 120px;
  display: block;
  background-color: #508d71;
  margin-left: 158px;
}

div#pro-box h1 {
  font-size: 100px;
  text-align: center;
  position: relative;
  top: 20px;
}

div#pro-box a {
  color: white;
  padding: 10px 12px;
  background-color: #508d71;
  border-radius: 4px;
  text-decoration: none;
  display: block;
  width: 40%;
  margin: auto;
  text-align: center;
  position: relative;
  top: 20%;
}

div.web {
  width: 100vw;
  height: 100vh;
}

section.middle {
  display: grid;
  grid-template-columns: 0.5fr 1fr;
  width: 98vw;
  height: 75%;
  margin: auto;
  border-radius: 5px;
  grid-column-gap: 15px;
  margin-top: 10px;
}
/* Header Style Below */

header {
  width: 98vw;
  margin: auto;
  border-radius: 5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 5%;
  background-color: #2b2e2c;
  margin-top: 10px;
}

header ul li {
  display: inline-block;
  padding: 7px;
}

header li a {
  color: white;
  text-decoration: none;
}

header li a.active, header li a:hover {
  color: #508d71;
  transition: color 0.4s ease-in-out;
}

/* NavBar Style Below */

nav {
  width: 98vw;
  margin: auto;
  padding: 10px 5%;
  background-color: #2b2e2c;
  margin-top: 10px;
  border-radius: 5px;
  display: flex;
  justify-content: center;
}

nav ul li {
  display: inline-block;
  padding: 10px;
  transition: color 0.4s ease-in-out;
  cursor: pointer;
}

nav li:hover {
  color: #508d71;
}

/* Footer Styles Below  */

footer {
  width: 98vw;
  margin: auto;
  background-color: #2b2e2c;
  color: white;
  text-align: center;
  padding: 5px 5%;
  border-radius: 5px;
  margin-top: 15px;
  margin-bottom: 10px;
}

/* SideBar/Main Style Below */

aside, main {
  background-color: #2b2e2c;
  color: white;
}

aside ul li {
  list-style: none;
}
